<template>
  <div ref="box" class="box" :style="{height: `calc(100vh - 140px - ${extraMinusHeight}px)`}">
    <!-- 横向拖拽拉动 -->
    <template v-if="mode === 'horizontal'">
      <div :class="['left', leftClassName]" :style="{height: `calc(100vh - 140px - ${extraMinusHeight}px)`}">
        <!--左侧div内容-->
        <el-scrollbar class="scroll-drawer">
          <div class="part-inner" :style="{minHeight: `calc(100vh - 170px - ${extraMinusHeight}px)`}">
            <slot name="left" />
          </div>
        </el-scrollbar>
      </div>
      <div class="resize" title="收缩侧边栏">
        ⋮
      </div>
      <div :class="['mid', `${leftClassName}-right`]" :style="{height: `calc(100vh - 140px - ${extraMinusHeight}px)`}">
        <!--右侧div内容-->
        <el-scrollbar class="scroll-drawer">
          <div class="part-inner part-inner-right" :style="{minHeight: `calc(100vh - 186px - ${extraMinusHeight}px)`}">
            <slot name="right" />
          </div>
        </el-scrollbar>
      </div>
    </template>
    <!-- 纵向拖拽拉动 -->
    <template v-else>
      <div ref="topTable" class="top">
        <slot name="left" />
      </div>

      <div ref="lineDiv" class="lineDiv" style="background-color:#DCDCDC;height:5px" />
      <div ref="dTable" class="bottom">
        <slot name="right" />
      </div>

    </template>
  </div>
</template>

<script>

const LEFT_WEIGHT_INIT = {
  1: 'one-third', // 三分之一
  2: 'half', // 一半
  3: 'two-thirds', // 三分之二
  4: 'purple', // 采购订单左侧固定宽度
  5: 'warehouse-location', // 库位左侧固定宽度
  6: 'item-group', // 物料组左侧固定宽度
  7: 'inspection-start' // 物资开始检验左侧固定宽度
}

export default {
  name: 'DragScreenComs',
  props: {
    mode: {
      type: String,
      default: 'horizontal'
    },
    leftWeight: {
      type: Number,
      default: 1
    },
    // 非全屏状态下，在全屏高度下需要额外减去的高度
    extraMinusHeight: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {}
  },
  computed: {
    leftClassName(newVal) {
      return LEFT_WEIGHT_INIT[this.leftWeight]
    }
  },
  mounted() {
    if (this.mode === 'horizontal') {
      this.dragHorizontal()
    } else {
      this.dragVertical()
    }
  },
  methods: {
    // 横向拖动
    dragHorizontal() {
      var resize = document.getElementsByClassName('resize')
      var left = document.getElementsByClassName('left')
      var mid = document.getElementsByClassName('mid')
      var box = document.getElementsByClassName('box')
      for (let i = 0; i < resize.length; i++) {
        // 鼠标按下事件
        resize[i].onmousedown = function(e) {
          // 颜色改变提醒
          resize[i].style.background = '#818181'
          var startX = e.clientX
          resize[i].left = resize[i].offsetLeft
          // 鼠标拖动事件
          document.onmousemove = function(e) {
            var endX = e.clientX
            var moveLen = resize[i].left + (endX - startX) // （endx-startx）=移动的距离。resize[i].left+移动的距离=左边区域最后的宽度
            var maxT = box[i].clientWidth - resize[i].offsetWidth // 容器宽度 - 左边区域的宽度 = 右边区域的宽度

            if (moveLen < 32) moveLen = 32 // 左边区域的最小宽度为32px
            if (moveLen > maxT - 150) moveLen = maxT - 150 // 右边区域最小宽度为150px

            resize[i].style.left = moveLen // 设置左侧区域的宽度

            for (let j = 0; j < left.length; j++) {
              left[j].style.width = moveLen + 'px'
              mid[j].style.width = (box[i].clientWidth - moveLen - 10) + 'px'
            }
          }
          // 鼠标松开事件
          document.onmouseup = function(evt) {
            // 颜色恢复
            resize[i].style.background = '#d6d6d6'
            document.onmousemove = null
            document.onmouseup = null
            resize[i].releaseCapture && resize[i].releaseCapture() // 当你不在需要继续获得鼠标消息就要应该调用ReleaseCapture()释放掉
          }
          resize[i].setCapture && resize[i].setCapture() // 该函数在属于当前线程的指定窗口里设置鼠标捕获
          return false
        }
      }
    },
    // 纵向拖动
    dragVertical() {
      var tableDiv = this.$refs.topTable
      console.log(tableDiv.offsetHeight)

      var dtable = this.$refs.dTable

      var oDiv = this.$refs.lineDiv

      oDiv.onmousedown = function() {
        var theight = parseInt(tableDiv.offsetHeight)// parseInt为了不指向对象

        var dheight = parseInt(dtable.offsetHeight)

        oDiv.style.cursor = 's-resize'

        const ev = ev || event

        // 鼠标按下坐标

        // var mouseDownX = ev.clientX

        var mouseDownY = ev.clientY

        // IE8 取消默认行为-设置全局捕获

        if (oDiv.setCapture) {
          oDiv.setCapture()
        }

        document.onmousemove = function(ev) {
          ev = ev || event

          // 鼠标移动时的鼠标位置

          // var mouseMoveX = ev.clientX

          var mouseMoveY = ev.clientY

          tableDiv.style.height = theight + (mouseMoveY - mouseDownY) + 'px'

          dtable.style.height = dheight + (mouseDownY - mouseMoveY) + 'px'
        }
      }

      document.onmouseup = function() {
        document.onmousemove = null

        // 释放全局捕获

        if (oDiv.releaseCapture) {
          oDiv.releaseCapture()
        }
      }

      return false
    }
  }
}
</script>

<style lang='scss' scoped>
/* 拖拽相关样式 */
/*包围div样式*/
.box {
    width: 100%;
   //  height: calc(100vh - 140px);
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.11);
}
/*
*   横向拖拽模式样式
*
*/
/*左侧div样式*/
.left {
    // width: calc(32% - 10px);  /*左侧初始化宽度*/
    height: calc(100vh - 140px);
    overflow-y: auto;
    background: #FFFFFF;
    float: left;
}
// 屏幕的几种尺寸   左面三分之一   左面一半    左面三分之二
.one-third{
  width: calc(32% - 10px);
}
.one-third-right{
  width: 68%;
}
.half{
  width: calc(50% - 10px);
}
.half-right{
   width: 50%;
}
.two-thirds{
  width: calc(68% - 10px);
}
.two-thirds-right{
  width: 32%;
}
.purple {
  width: calc(25% - 10px);
}
.purple-right {
  width: 75%;
}
.warehouse-location {
  width: calc(20% - 10px);
}
.warehouse-location-right {
  width: 80%;
}
.item-group {
  width: calc(25% - 10px);
}
.item-group-right {
  width: 75%;
}
.inspection-start {
  width: calc(25% - 10px);
}
.inspection-start-right {
  width: 75%;
}
/*拖拽区div样式*/
.resize {
    cursor: col-resize;
    float: left;
    position: relative;
    top: 45%;
    background-color: #d6d6d6;
    border-radius: 5px;
    margin-top: -10px;
    width: 10px;
    height: 50px;
    background-size: cover;
    background-position: center;
    /*z-index: 99999;*/
    font-size: 32px;
    color: white;
}
/*拖拽区鼠标悬停样式*/
.resize:hover {
    color: #444444;
}
/*右侧div'样式*/
.mid {
    float: left;
    // width: 68%;   /*右侧初始化宽度*/
    // height: 100%;
    height: calc(100vh - 140px);
    overflow-y: auto;
    background: #fff;
    box-shadow: -1px 4px 5px 3px rgba(0, 0, 0, 0.11);
    position: relative;
    z-index: 1999;
}
.part-inner{
  padding: 14px;
  min-height: calc(100vh - 170px);
  overflow-x: auto;
}
.part-inner-right{
  padding: 8px 15px;
  min-height: calc(100vh - 186px);
}
::v-deep .scroll-drawer {
  height: 100%;
}
// ::v-deep .el-scrollbar .el-scrollbar__wrap .el-scrollbar__view{
//    white-space: nowrap;
// }
::v-deep .el-scrollbar__wrap {
  overflow-x: hidden;
}
/*
*   纵向拖拽模式样式
*
*/
.top{
  width: 100%;
  height: calc(100vh / 2);
  overflow-y: auto;
  background: #FFFFFF;
  float: left;
}
</style>
